.popup__close {
    position: absolute;
    margin: 0;
    top: -40px;
    right: -40px;
    width: 32px;
    height: 32px;
    object-fit: contain;
}

@media (max-width: 580px) {
    .popup__close {
        top: -36px;
        right: 0;
        width: 20px;
        height: 20px;
    }
}