.form__input {
  box-sizing: border-box;
  display: block;
  height: 27px;
  padding: 0px 0px 11px;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  font-weight: 400;
  font-size: 14px;
  color: #000000;
  margin-top: 30px;
  width: 100%;
}

.form__input:focus {
  outline: none;
}
